import React, { Component } from 'react';
import './App.css';
import Flexbox from 'flexbox-react';
import pixr02 from './imgs/pixr02.jpg'
import vcbc1 from './imgs/vcbc1.jpg'
import vcbc2 from './imgs/vcbc2.jpg'
import rt from './imgs/rt.jpg'
import headshot from './imgs/headshot.png'
import cv from './imgs/macaldo_resume_02_20_2018.pdf'

class App extends Component {
  constructor(props) {
   super(props);
   this.aboutClick = this.aboutClick.bind(this);
   this.mainClick = this.mainClick.bind(this);

   this.state =
     {
       main: true,
       about: false
     };



 }

  aboutClick() {
     this.setState({ about: true, main: false });

   }

 mainClick() {
    this.setState({ about: false, main: true });

  }




  render() {
    return (
      <div className="App">
        <Flexbox style={styles.mainFlex}>

          <div style={styles.nav}>
            <hr style={styles.hr} />
            <div style={styles.logo}>
              JOVEN <br />
            <div style={styles.lastname}>MACALDO</div>
            </div>
            <p style={styles.tag}>FULL-STACK WEB DEVELOPER</p>
            <ul style={styles.ul}>
              <li onClick={this.aboutClick}>ABOUT</li>
              <li onClick={this.mainClick}>PAST PROJECTS</li>
              <li><a style={styles.link}  href="mailto:jmacaldo@me.com">MAIL</a></li>
              <li><a style={styles.link}  href={cv}>DOWNLOAD CV</a></li>
              <li><a style={styles.link} target="_blank" href="https://github.com/jmacaldo">GITHUB</a></li>
              <li><a style={styles.link} target="_blank" href="http://www.linkedin.com/in/jovenmacaldo">LINKEDIN</a></li>
              <li><a style={styles.link} target="_blank" href="https://www.instagram.com/jovenmacaldo/">INSTAGRAM</a></li>
            </ul>
          </div>
          <div style={styles.content}>
            <hr style={styles.hr} />
            <div style={styles.projects}>


            {this.state.main &&
              <div>
                <h3>PAST PROJECTS</h3>

                <img style={styles.img} src={pixr02} />
                <Flexbox style={styles.flexcontainer}>
                  <div>
                    <h3>PROJECT NAME</h3>
                    <h5 style={styles.value}>PIXR</h5>
                    <h3>LINKS</h3>
                    <h5 style={styles.value}><a target="_blank" href="https://pixr0.herokuapp.com/">LIVE DEMO</a></h5>
                    <h5 style={styles.value}><a target="_blank" href="https://github.com/Pixr0/pixr">SOURCE</a></h5>
                  </div>
                  <div style={styles.right}>
                    <h3>TECHNOLOGIES</h3>
                    <h5 style={styles.value}>HTML5, CSS, JavaScript, Node.Js, PostgreSQL, EJS, Squelize, Passport.Js</h5>
                    <h3>DESCRIPTION</h3>
                    <h5 style={styles.value}>Pixr is a project that I have done while in coding bootcamp. This project demonstrates skills in database management, user authentication, responsive design, and the use of the EJS templating engine.  Pixr is an Instagram-like web app where users can share and comment on photos.  </h5>
                  </div>
                </Flexbox>


                <img style={styles.img} src={vcbc1} />
                <Flexbox style={styles.flexcontainer}>
                  <div>
                    <h3>PROJECT NAME</h3>
                    <h5 style={styles.value}>VEGAN COOK BOOK CLUB</h5>
                    <h3>LINKS</h3>
                    <h5 style={styles.value}><a target="_blank" href="https://vcbc.herokuapp.com/">LIVE DEMO</a></h5>
                    <h5 style={styles.value}><a target="_blank" href="https://github.com/jmacaldo/vcbc">SOURCE</a></h5>
                  </div>
                  <div style={styles.right}>
                    <h3>TECHNOLOGIES</h3>
                    <h5 style={styles.value}>HTML, CSS, JavaScript, Node.Js, PostgreSQL, Squelize, React-Redux, RESTful APIs </h5>
                    <h3>DESCRIPTION</h3>
                    <h5 style={styles.value}>
                      VCBC is a web app that allows users to share and rate their favorite plant-based recipes. I have solely built this project from the ground up. Main features include: user authentication, API-powered aggregated search, and the ability to rate and comment on recipes.
                    </h5>
                  </div>
                </Flexbox>

                <img style={styles.img} src={rt} />
                <Flexbox style={styles.flexcontainer}>
                  <div>
                    <h3>PROJECT NAME</h3>
                    <h5 style={styles.value}>OMDB SEARCH APP</h5>
                    <h3>LINKS</h3>
                    <h5 style={styles.value}><a target="_blank" href="http://omdbnycda.herokuapp.com/">LIVE DEMO</a></h5>
                    <h5 style={styles.value}><a target="_blank" href="https://github.com/RottenTomatoesBoard/master">SOURCE</a></h5>
                  </div>
                  <div style={styles.right}>
                    <h3>TECHNOLOGIES</h3>
                    <h5 style={styles.value}>HTML, CSS, JavaScript, Node.Js, PostgreSQL, Squelize, React-Redux, RESTful APIs </h5>
                    <h3>DESCRIPTION</h3>
                    <h5 style={styles.value}>
                      This is a movie search app that uses the OMDB api as its search backend. This project was done in coordination with two other developers. I was personally responsible for the comments section and the deployment of the of the app.
                    </h5>
                  </div>
                </Flexbox>
              </div>
            }
            {this.state.about &&
              <div>
                <h3>ABOUT ME</h3>

                <Flexbox style={styles.aboutColumns}>
                  <div>
                    <h5 style={styles.aboutLeft}>
                      <p>
                        Hi! Im Joven Macaldo - a web developer and former photographer who brings with him a  strong aesthetic sense to web development. Im a tinkerer since childhood who loves to take things apart to find out how they work. I still do the same thing today, but only now with websites.
                      </p>

                      <p>
                        I consider the browsers window as akin to a cameras viewfinder - with divs and other elements as its subjects. Unlike a photograph however, websites are not a static medium. Subjects move. Even the size and shape of the very canvas the work exists in changes. To me, therein lies the excitement and challenge of web development in 2018. I believe that websites should not only function well, but also look great no matter the medium.
                       </p>

                       <p>
                           I am well versed in HTML5, CSS, JavaScript, and React. Although a currently a junior developer, I consider myself a quick study, and I relish the process of learning new technologies to solve problems.
                       </p>

                       <p>
                           When not pushing code, I enjoy some competitive cycling on the weekends. Check out my racing team HERE. I live in Brooklyn, NY with my wife Martina and a cat named Janek.
                       </p>


                    </h5>
                  </div>
                  <div>
                    <h5 style={styles.aboutContainer}>





                    </h5>
                  </div>
                </Flexbox>
              </div>
            }
            </div>
          </div>

        </Flexbox>

      </div>
    );
  }
}



const styles = {
  nav: {
    width: 300,
    paddingTop: 50,
    paddingLeft: 50
  },
  logo: {
    fontSize: 50
  },
  lastname: {
    color: "#808080",
    marginTop: -20
  },
  headshot: {
    width: 100
  },
  tag: {
    marginTop: -10
  },
  projects: {
    display: 'flex',
    flexDirection: 'column',
  },
  value: {
    marginTop: -20
  },
  right: {
    paddingLeft: 30
  },

  content: {
    paddingTop: 50,
    paddingLeft: 50,
    width: 860,
    paddingRight: 50
  },
  link: {
    textDecoration: "none",
    color: "#808080",
    ":hover": {
      color: "blue"
    }
  },
  aboutContainer:{

  },

  aboutColumns: {

  },
  ul: {
    listStyleType: "none",
    marginLeft: -35,
    marginTop: 40,
    color: "#808080",
    textDecoration: "none"
  },
  mainFlex: {
    flexWrap: "wrap",
    width: '100vw'
  },
  container: {
    display: 'flex',
},
flexcontainer: {
  marginBottom: 50,
  width: '80%'
},
aboutLeft: {
  paddingRight: 20,
  width: '50%'
},
inner: {
  flex: '0 1 auto',
  display: 'flex',
  position: 'absolute',
  width: 860,
  justifyContent: 'center',
},
img: {
  flex: '0 1 auto',
  width: '100%',
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)"
},
  hr: {
    border: 0,
    height: 0,
    borderTop: "1px solid rgba(0, 0, 0, 1)",
    borderBottom: "1px solid rgba(255, 255, 255, 1)",
    color: '#fff'
}
}


export default App;



